import React, { useEffect, useState } from 'react';
import './WateringChart.css';
import { Chart as ChartJS, CategoryScale, LinearScale, PointElement, Tooltip, Legend } from 'chart.js';
import { Scatter } from 'react-chartjs-2';

ChartJS.register(CategoryScale, LinearScale, PointElement, Tooltip, Legend);

function WateringChart() {
    const [logData, setLogData] = useState([]);

    useEffect(() => {
        fetch('/api/pump/logs?range=7d')
            .then((res) => res.json())
            .then((data) => {
                const parsed = data.map((item) => ({
                    x: new Date(item.timestamp).toLocaleDateString('ko-KR', {
                        month: '2-digit',
                        day: '2-digit',
                    }),
                    y: item.method === 'auto' ? 1 : 2,
                }));
                setLogData(parsed);
            });
    }, []);

    const chartData = {
        datasets: [
            {
                label: '자동 급수',
                data: logData.filter((d) => d.y === 1),
                backgroundColor: '#10b981',
                pointRadius: 6,
                showLine: false,
            },
            {
                label: '수동 급수',
                data: logData.filter((d) => d.y === 2),
                backgroundColor: '#3b82f6',
                pointRadius: 6,
                showLine: false,
            },
        ],
    };

    const chartOptions = {
        responsive: true,
        plugins: {
            legend: {
                position: 'bottom',
            },
            tooltip: {
                callbacks: {
                    label: (context) => {
                        const type = context.dataset.label;
                        return `${type} → ${context.parsed.x}`;
                    },
                },
            },
        },
        scales: {
            x: {
                type: 'category',
                grid: { color: '#f3f4f6' },
            },
            y: {
                ticks: {
                    callback: (value) => (value === 1 ? 'auto' : value === 2 ? 'manual' : ''),
                },
                min: 0.5,
                max: 2.5,
                grid: { color: '#f3f4f6' },
            },
        },
    };

    return (
        <div className="watering-card">
            <Scatter data={chartData} options={chartOptions} />
        </div>
    );
}

export default WateringChart;
