import React, { useEffect, useState } from 'react';
import SensorChart from '../components/Chart/SensorChart';
import WateringChart from '../components/Chart/WateringChart';
import './Report.css';

function Report() {
    const [sensor, setSensor] = useState({});
    const [mood, setMood] = useState({});

    useEffect(() => {
        fetch('/api/sensor/latest')
            .then((res) => res.json())
            .then((data) => setSensor(data));

        fetch('/api/mood/latest')
            .then((res) => res.json())
            .then((data) => setMood(data));
    }, []);

    const now = new Date();
    const day = now.toLocaleDateString('ko-KR', { weekday: 'long' });
    const time = now.toLocaleTimeString('ko-KR', {
        hour: '2-digit',
        minute: '2-digit',
        hour12: true,
    });

    return (
        <div className="report-container">
            {/* ✅ 제목: 최근 센서 기록 */}
            <h3 className="section-title1">📌 최근 센서 기록</h3>

            {/* ✅ 카드 한 줄 */}
            <div className="metrics-row">
                <div className="metric-card">
                    <div className="metric-label">🌡️ 온도</div>
                    <div className="metric-value">{sensor.temperature ?? '--'}℃</div>
                </div>
                <div className="metric-card">
                    <div className="metric-label">💧 습도</div>
                    <div className="metric-value">{sensor.humidity ?? '--'}%</div>
                </div>
                <div className="metric-card">
                    <div className="metric-label">🌱 토양</div>
                    <div className="metric-value">
                        {sensor.soil_dry === false ? '적당함' : sensor.soil_dry === true ? '건조함' : '--'}
                    </div>
                </div>
                <div className="metric-card">
                    <div className="metric-label">🌞 조도</div>
                    <div className="metric-value">
                        {sensor.light === 'bright' ? '밝음' : sensor.light === 'dark' ? '어두움' : '--'}
                    </div>
                </div>
                <div className="metric-card">
                    <div className="metric-label">😊 기분</div>
                    <div className="metric-value">{mood.mood ?? '--'}</div>
                </div>
                <div className="metric-card">
                    <div className="metric-label">🕒 {day}</div>
                    <div className="metric-value">{time}</div>
                </div>
            </div>

            {/* ✅ 제목: 최근 일주일 센서 변화 */}
            <h3 className="section-title2">📈 최근 일주일 내 센서 변화 그래프</h3>

            {/* ✅ 센서 추이 라인 차트 */}
            <SensorChart />

            <h3 className="section-title2">⛲ 최근 일주일 급수 기록</h3>
            <WateringChart />
        </div>
    );
}

export default Report;
